/* harmony export (binding) */ __webpack_require__.d(__webpack_exports__, "overviewList", function() { return overviewList; });
var overviewList=[{'icon':__webpack_require__(922),'title':'走进禹州','details':[{'type':'image','value':__webpack_require__(923)},{'type':'content','value':['禹州市被誉为华夏第一都，以钧瓷文化、大禹文化、中医药文化著称。历史上，这里是中国第一个奴隶制王朝——夏朝的建都地、中国“五大名瓷”之钧瓷的唯一产地，同时也是明清时期全国四大中药材集散地之一，素有“夏都”、“钧都”、“药都”之称。禹州市社会经济快速发展，位列全国各项综合实力百强。','禹州市境内现存地面历史文化遗存2420处，位列全省县级市第一；拥有国家非物质文化遗产2项、省级非物质文化遗产8项。1989年被命名为河南省首批历史文化名城，2006年、2007年先后被命名为“中国陶瓷文化之乡”和“中国大禹文化之乡”，2008年被列入全省首批文化改革发展试验区，2011年被命名为“中国陶瓷历史文化名城”，2013年11月荣膺“中国中原瓷都”称号。2014年10月，禹州入选国家新型城镇化综合试点。2016年12月，被列为第三批国家新型城镇化综合试点地区。2017年10月，被命名为“国家园林城市”。2017年12月，当选中国工业百强县。']}]},{'icon':__webpack_require__(924),'title':'历史沿革','details':[{'type':'title','value':'商周'},{'type':'content','value':['禹境称“夏邑”或“夏国”，亦称“虞国”。舜时期，禹在此受封为夏伯。禹的儿子启又于此创建了第一个奴隶制王朝，并大飨诸侯于钧台。《水经注》载：“河南阳翟县有夏亭城，夏禹始封于此，为夏国”。《竹书纪年》载：“夏禹之子夏启，即位夏邑，大享诸侯于钧台，诸侯从之”。后夏迁都山西安邑。帝相失国后，少康中兴，也由夏众灭浞，奉少康归于夏邑，诸侯始闻之，立为天子，建都于少康城(禹州市顺店镇康城村)。故在史籍中，早期多称禹州为夏邑。','商朝置历国仍为夏邑，商汤曾将夏禹的后裔封于此。西周以此地为历邑，周武王时，封武王之弟于康(禹州市顺店镇康城村)。禹州在春秋时，为郑国别都栎邑。周襄王十六年（公元前636年），北方翟人（亦称狄人）入据栎地，因其地在嵩山之阳，改称阳翟（音狄）。周威烈王十八年（公元前408年），韩景侯虔将韩国国都自平阳迁至阳翟。韩哀侯二年（公元前375年），韩灭掉郑，遂迁都郑（今新郑市）。五年之后，在周烈王六年（公元前370年），韩懿侯将国都复迁阳翟。']},{'type':'title','value':'先秦两汉'},{'type':'content','value':['秦始皇十七年（公元前230年），秦使内史腾灭韩，虏韩王安，以其地置颍川郡，郡治首府设在阳翟。秦二世二年（公元前208年），项梁立韩公子成为韩王，仍置都于阳翟。秦末，项羽、刘邦先后攻占阳翟，并分别封韩室后裔为王，都于此。','汉高祖五年（公元前202年），刘邦立韩王信为韩王，置都阳翟。高祖六年（公元前201年）徙韩王信于太原，复以韩国之地为颍川郡，郡治首府设在阳翟，领县二十（阳翟、昆阳、颍阳、定陵、长社、新汲、襄城、郾城、郏县、舞阳、颍阳、高、许、鄢陵、临颍、父城、成安、周承休、阳城、纶城）。','汉武帝时，全国分为13个州，阳翟属豫州颍川郡，郡治阳翟，领18县，后宣帝和元帝时，增颍川郡属县分别为19和20个。','新莽时期，王莽复古改制，在天凤元年(公元14年)，将颍川改为左队郡，辖区与郡治未变。东汉光武帝建武元年（公元25年），改左队郡为颍川郡，归豫州治所仍置阳翟，领县十八。']},{'type':'title','value':'三国'},{'type':'content','value':['三国时期魏、蜀、吴三国分治，阳翟属魏。魏黄初元年(220年)，阳翟改属豫州部河南郡。']},{'type':'title','value':'两晋南北朝'},{'type':'content','value':['西晋时期属司州部河南郡。东晋十六国时，阳翟曾先后属东晋（改阳翟县为阳翟郡）、前赵、后赵、前燕、前秦、后燕、后秦等政权。南北朝初，阳翟属南朝宋，归豫州，颍川郡寄治睢阳，领阳翟。北魏孝昌二年（526年），阳翟分为阳翟、阳城两郡。东魏孝静帝兴和元年（539年），原阳翟县划为阳翟、黄台（今郭连镇黄台村）两县，均属阳翟郡。']},{'type':'title','value':'隋唐五代'},{'type':'content','value':['隋开皇三年（583年），废阳翟郡，以阳翟属襄城郡，隶汝州。大业元年（605年）黄台县并入阳翟县，隶属豫州部襄城郡管辖。唐武德三年（620年），析阳城、嵩阳、阳翟各一部，置康城县。贞观三年（629年），废康城县，并入阳翟县。唐高宗龙朔二年(662年)，阳翟县又改归河南府洛州管辖。唐武宗会昌三年(843年)，阳翟复归许州颍川郡管辖。五代时期后梁时期，阳翟县属许州匡国军管辖。后唐、后晋、后汉、后周四代(923—960年)，阳翟县均属西京下属的许州忠武军管辖。']},{'type':'title','value':'宋金元明清'},{'type':'content','value':['北宋时阳翟县属京西北路颍昌府。宋靖康元年(1126年)，金兵入主中原，宋室南迁。阳翟县归金管辖。其间名称多变，建置归属复杂。','在金代伪齐刘豫统治时期，在阳翟县设颍川郡。','金大定二十年（1180年），颍顺军改为颍顺州，隶属南京路管辖(南京即河南省开封市)；阳翟县仍为其附郭。金大定二十四年（1184年），因州有古“钧台”遗迹，改颍顺州为钧州，仍属南京路。辖阳翟、新郑二县和一个郭店镇。','元顺帝至正元年（1341年），钧州改领阳翟、新郑、密县3县。','明洪武初年，撤销阳翟县制并入钧州州治（沿用1700余年的阳翟县名，至此告终），仍辖密县、新郑二县。明穆宗隆庆四年(1570年)，新郑从钧州划出，钧州仅领密县。','明神宗万历三年（1575年），为避神宗朱翊钧名讳，改钧州为禹州。明崇祯十六年(1643年)正月，李自成第二次攻下禹州，遂在禹州设均平府，辖27县，改禹州为均平县。','清初沿袭明制，仍设禹州，归开封府管辖，领密县一县。至雍正二年（1724）升禹州为直隶州，领新郑、密县二县。十二年降为散州，乾隆六年（1741）复隶属开封府。']},{'type':'title','value':'民国时期'},{'type':'content','value':['中华民国元年1913年3月1日，废府存县改禹州为禹县，隶豫东道。1914年6月2日，改隶开封道。1932年9月27日，改属河南第一行政督察区。1946年河南又划成12个区，禹县属第一区。','另，民国十七年（1928年）析登封县（今登封市）、禹县（今河南省禹州市）置民治县，治白沙（今禹州市花石镇白沙）。取资产阶级“民享、民有、民治”的主张中“民治”一词命名。1931年撤销，其地复归登封县、禹县。','1945年以后，共产党曾分别在禹县、郏县交界设禹郏县；在城北扒村一带设禹北县；在禹县、密县和新郑交界设密禹新中心县；1948年在许昌、禹县交界设许西县。存在时间较短，多未形成真正的建置和辖区。']},{'type':'title','value':'新中国成立'},{'type':'content','value':['中华人民共和国仍设禹县，先后隶属河南省许昌专署和许昌市。',' 1988年6月25日，国务院批准撤销禹县设立禹州市，为省直辖县级市，计划单列。']}]},{'icon':__webpack_require__(925),'title':'地理环境','details':[{'type':'title','value':'位置'},{'type':'content','value':['禹州市位于河南省中部，地处伏牛山脉与豫东平原过渡带，在东经113°03′-113°39′和北纬33°59′-34°24′之间。总面积1461平方公里。东接许昌市建安区、长葛市，北靠新郑市、新密市，西北邻登封市，西及南部连汝州市、郏县、襄城县。']},{'type':'title','value':'地势'},{'type':'content','value':['禹州整个地势由西北向东南倾斜。以横贯西北、东南的颍河为界，构成北（具茨）、南（箕山）两大山系，环抱颍川平原。']},{'type':'title','value':'气候'},{'type':'content','value':['禹州市属暖温带季风气候区，热量资源丰富，雨量充沛，光照充足，无霜期长。因属大陆性季风气候，多旱、涝、风、雹等气象灾害。','全市四季气候总的特征是：春季干旱多风沙；夏季炎热雨集中；秋季晴和气爽日照长；冬季寒冷少雨雪。','历年年平均气温在13.0℃—16.0℃之间。最暖年是1961年，年平均气温为15.7℃。较冷年是1984年，年平均气温为13.6℃，暖、冷年相差2.1℃。年极端最高气温为42.9℃，发生在1972年6月；年极端最低气温为-13.9℃，发生在1958年1月和1971年12月。最热月在7月份，平均气温为27.6℃；最冷月在1月，平均气温0.2℃。日平均气温稳定通过0℃以上的初日平均为2月11日，终日为12月21日，其间积温为5176.6℃。','历年年平均降水量为650毫米左右，最多年为1073.8毫米，发生在1967年；最少年为442.3毫米。由于受季风气候影响，各季节降水量分布悬殊，全年以夏季（6—8月）雨水最为集中，平均达353毫米，占年平均降水量的54%。秋季和春季雨量分别为151毫米和122毫米，占年降水量的23%和19%。冬季雨雪稀少，平均降水量仅25毫米，占年平均的4%。','禹州市处于大陆季风区，风向、风速均有明显的季节变化。年平均风速每秒为2.5米。夏季多偏南风，冬季多偏北风，常年主要风为东北风。历年年平均无霜期为218天，最长达248天（1977年），最短只有179天（1962年）。最低气温≤2.0℃为霜冻指标，霜冻平均初日为11月1日，平均终日在4月5日，间隔日数为155天。']},{'type':'title','value':'水文'},{'type':'content','value':['禹州市属淮河流域沙颍河水系，境内主要行洪沟河31条，承担着辖区各乡（镇、办）的行洪除涝任务。流域面积大于50km的河流15条，河流总长约324.2km，其中：流域面积超过3000km的有颍河，境内河长约59.5km；流域面积200~3000km的河流有兰河、肖河、吕梁江、石梁河等4条河流，累计境内河长约71.0km；流域面积50~200km的河流有小泥河、高底河、红河、白水河、扒村河、书堂河、龙潭河、潘家河、涌泉河、小青河等10条河流，河道总长约193.7km。50km以下的沟河有16条，它们是：磨河、犊水沟、尚沟河、水磨河、秦北沟、梁北沟、禁沟、石柱河、鸿畅石板河、苌庄石板河、马沟河、吓水河、柳河沟、吴河、和山房沟、花园河。']}]},{'icon':__webpack_require__(926),'title':'民风民俗','details':[{'type':'content','value':['神垕民间文艺（民间社火）主要有高跷、旱船、龙灯、舞狮、大响等，每逢节庆，则由各行业筹资聘请河南地方剧种的梆子、曲子、越调等剧团演出，其中当地各窑场自供自演的“一把泥”（窑工）剧团演艺及行头最好，也最著名。','社火，旧时多以神社或村，或以宗族为单位的群众业余文化组织、活动形式有狮子、龙灯或大响等，社首多由德高望重者担任，逢年过节，聚众庆贺。常见的有：','1、大响(又名铜器)包括有大鼓，大钹，大饶．大锣，群众称为“四大扇”。在每年春节和元宵节期间擂奏。','2、狮子，也叫狮子舞、狮分为“文狮”、“武狮”两种，神垕的狮子社远近闻名。','3、龙灯，龙舞俗称“龙灯”、旧时，多在岁臼欢舞，以祈四季风调雨顾。龙头用竹木割成骨架，彩纸棱糊，龙身用绘有鳞片的布蒙上，全身多单节，一般为11节。表演时有“龙腾长空”，“老龙盘窝”等动作。 ','4、高跷，俗称“踩高脚”。在神垕流传很广，现少见。城关各大街，每年春节期间都组织演出。','其它的还有竹马、旱船、焰火等。']}]},{'icon':__webpack_require__(927),'title':'民族宗教','details':[{'type':'content','value':'2011年年末禹州市有伊斯兰教、基督教、天主教、佛教、和道教等宗教活动场所75处。境内有基督教、伊斯兰教、天主教、佛教、道教五种宗教，信教群众七万余人，占禹境人口的6%以上，经批准开放的宗教活动场所76处，宗教活动涉及各个乡、镇、办和大部分行政村。有市（县）级爱国宗教社团5个（其中天主教、佛教、道教为筹备组织）。'}]},{'icon':__webpack_require__(928),'title':'文化艺术','details':[{'type':'title','value':'华夏古都'},{'type':'content','value':['禹州市历史悠久，是中华民族的重要发祥地之一。文化遗迹上古时期，禹州属于应龙氏部落领地。发祥于黄帝，昌明于大禹，传承于夏启。与中华5000年文明史同步。是“中国大禹文化之乡”。','禹州境内北部的具茨山脉(无梁镇)、西部的大鸿寨山脉，是以黄帝为首的有熊氏部落以及与黄帝有亲缘关系的方嫘氏部落、大鸿氏部落、风后氏部落的重要活动区域。黄帝的姻亲——方嫘氏部落领地，在禹州西部方山(今方山镇)。重要军事力量——大鸿部落，在城西大鸿寨山区(今鸠山大鸿寨风景区)。协助黄帝战蚩尤的重要军事首领——风后氏部落，在城北无梁一带(今无梁镇)。禹州市城区有“迎风阁”，以迎风阁为中心，南北东西辐射四条街，分别叫迎上街，迎下街，迎东街，迎西街。建“迎风阁”的本意，一说是因为风后辅佐黄帝有功，封其食邑于此，特建阁以纪念；一说是“迎送风后”之意。','距城12公里的古城寺，古为氏城，雍梁城，本是轩辕黄帝命雍父做杵、臼之处，后来成为雍父的封地。','史家言：尧舜传贤，禹传子。从此以后，禹州成为中国最早的奴隶制王朝的国都。','夏邑(禹州)作为大禹的封国，大禹治水的基地，留下很多关于大禹活动的遗迹。据史籍记载：禹安民治室，居靡山伐木为邑。这便是大禹即位后建造的宫室。靡山今称梅山岗，位于范坡乡境内。禹之子启继位后，在此建立了中国历史上第一个奴隶制国家--夏，史称夏朝，因此禹州素有“华夏第一都”之誉。至今留存有夏禹和启召集群臣的古钧台，治水遗迹禹王锁鲛井，禹母石，等历史遗迹。而全国重点文物保护单位禹州瓦店遗址作为夏代的都城级王城遗址、夏商周断代工程、中华文明探源工程的最重要发现之一，为禹州是夏朝都城提供了直接的历史证据。','尧舜时期，禹州称夏，是夏部族聚居与活动的中心。大禹为夏部族首领，是黄帝的玄孙。禹总结了他父亲治水的经验教训，改用开 挖疏导为主，居外十三年，终于使洪水东注，治水成功，被封于夏（今禹州市），史称“夏禹国”。禹被称为“夏伯”，即分封方国国君。癸西四十八载（约公元前2208年），舜崩于苍梧（今云南省宁远），天下诸侯不服舜之子而拥戴禹。于是，禹在夏邑（今禹州市）即天子位，并用自己的封号“夏”为天下之号，称“夏朝”。这是中国历史上第一个以中原地区诸夏民族为主体，容纳吸收周边夷夏各部族共同创立的多民族统一的大国。它“咸有九州”，“协和万邦”，是我国从原始社会进入奴隶制社会的第一朝代，被称为“华夏第一国”。大禹则是中国历史上第一位帝王，后人称之为伯禹、夏禹、神禹。禹州就是因为大禹初封于此，故称“大禹之州”。后因夏王朝创立并建都于此，又称“夏都”或“华夏第一都。”','禹在夏都（今禹州市）执政八年，号令全国实行统一的夏时制，俗称“夏历”，也叫“农历”。并且定车制，立贡法，建学校，作夏乐，提倡养老，令行薄葬，实行“五音听治”。加上其它许多行之有效的措施，将一个新生的夏朝治理得井井有条，殷盛富庶，“公家有三十年之积，私家有九载之贮”。中国大禹文化之乡约公元前2198年，禹百岁，巡狩江南，死于会稽，就地埋葬。其子夏启“大飨诸侯于钧台”，四方诸侯都拥护夏启继禹之位，是所谓“父传子，家天下”的开始。启在位十年，传给其子太康，太康在位二十九年（有记四年），传给其弟仲康。仲康名义上在位十三年，传统给其子相。相在位二十八年，被寒浞逼杀。夏朝从此中断四十年。直到相的遗腹子少康长大后，居于纶邑（今禹州西北16公里康城），招集夏遗民，联络夏旧臣，经过十年准备，终于在不惑之年消灭了寒浞势力，“整威仪东南行，求阳翟（今禹州）夏王之故都”，“坐钧台而朝诸侯”恢复夏朝基业，史称“少康中兴”。此后，夏朝又传十一主，至夏桀，共十七世，有王与无王，累计维系了四百七十一年。','夏启会诸侯于城北诸侯山(无梁镇北)，大飨诸侯于钧台(梁北镇杜岗寺村)。从大禹时代起便出现了较完整的行政区划和赋税条律。禹州在上古时期就已经成为政治、经济、文化、军事的活动中心。国家的框架已基本形成。','夏朝第三帝名相，被大臣寒浞逼杀，致使夏统治中断40年。少康中兴，改都纶邑(今禹州顺店镇康城村)。最后一帝桀，将商部落首领汤囚禁于钧台(在梁北镇杜岗寺村)。据“中国监狱史”记载：中国第一座监狱即钧台。商灭夏后，禹州作为首都的地位开始变迁。','周威烈王十八年（公元前408年），韩景侯将韩国国都自平阳迁至阳翟。公元前375年，韩国攻灭郑国，遂将国都迁郑。仅五年，即公元前370年，韩懿侯便将国都城重又迁回阳翟。此后历秦、汉、三国、两晋、南北朝、隋、唐、宋、元等二千余年，禹州称阳翟不变。所以，许多文献史籍在记述历史事件及地地域时，习惯将上古时期尚无阳翟名称的夏、夏邑、历地、栎邑等统称为“阳翟”。']},{'type':'title','value':'钧瓷之都'},{'type':'content','value':['宋代有五大名窑：钧、汝、官、哥、定。钧瓷色彩缤纷，窑变美妙，位居诸窑之冠。《增补古今瓷器源流考》曰：“官汝哥定，器皆单色，而钧窑则众色参互，五彩缤纷”。其始质细腻，坚固敦实；扣之有声，声音清脆动听，犹如金属；釉色莹润，艳丽绝伦。有的窑变成自然美景，宛如仙山玉阁，月夜辰星；有的如彩虹闪现，焰火怒放。更为神奇的是钧瓷釉色中还常常出现珍珠点、冰片纹和蚯蚓走泥纹等，为钧瓷锦上添花。到2012年在全国大中城市都有钧瓷专卖店，在禹州销售企业主要集中在钧官窑路和神垕镇。每年一度的禹州中国钧瓷文化节都会评出当年的珍品与精品，在2005年钧瓷文化节上，评出了孔家钧窑、荣昌钧窑、禹州市钧瓷研究所等7家中国钧瓷名窑。','中国是瓷器的故乡，而禹州则是中国瓷器的重要发祥地，是著名的“中国陶瓷文化之乡”。其陶艺制作已有1700多年历史，而钧瓷又以其贵、神、奇、妙四大特点堪称瓷中极品，凭借其古朴的造型、精湛的工艺、复杂的配釉、“入窑一色出窑万彩”的神奇窑变，湖光山色、云霞雾霭、人兽花鸟虫鱼等变化无穷的图形色彩和奇妙韵味，被誉为中国“五大名瓷”之首，素有“黄金有价钧无价”、“家有万贯，不如钧瓷一片”之说。','瓷器钧瓷，以其色彩艳丽、艺术风格独特闻名中外，久享盛誉。在国外，钧瓷被称为“中国宝瓷”，是难得的艺术珍品。钧瓷不但在色彩上鹤立它瓷之上，在造型和施釉方面也独辟蹊径，自成体系。钧瓷的名贵在于其独特的窑变釉色，其釉色是自然形成，非人工描绘，每一件钧瓷的釉色都是唯一的，独一无二的，即“ 钧瓷无双” ，且釉透，釉活，胎质精纯，坚实细腻。叩之声圆润悦耳，清脆动听；观之形端庄优美，古朴典雅，它的釉变色五彩缤纷，璀璨夺目，浑然天成，构成一幅幅神奇的图画，如寒鸦归林，夕阳残照，高山云雾，峡谷飞瀑，伯牙抚琴等等。这些釉变画，千变万化，意味无穷，具有千古耐看的魅力，名人曾用“ 出窑一幅元人画，落叶寒林返暮鸦”“ 雨过天晴泛红霞，夕阳紫翠忽成岚”“ 峡谷飞瀑菟丝缕，窑变奇景天外天” 等来形容钧瓷窑变之妙，民间有“ 钧与玉比，钧比玉美，似玉非玉胜似玉” 、“ 黄金有价钧无价” ，国外更有“ 雅堂无钧瓷，不可自夸富” 、“ 纵有家财万贯，不如钧瓷一片” 之说。','钧瓷的烧制是一种复杂的高难技术，还原程度很难控制；加上过去由于使用的是松木窑，窑温难以达到均衡，从而影响到钧瓷釉彩的成色，在烧制过程中有70%的产品会报废掉，而其中的上品则更为罕见，再加上至今已有1300多年历史的钧瓷，自古就有 “黄金有价钧无价 ”的尊贵名声，唐玄宗曾立令“ 钧不随葬” ；宋徽宗时又成为御用珍品，诰封“ 神钧宝瓷” ，每年钦定生产36件，禁止民间收藏，所以禹州墓葬出土的钧瓷文物也甚为稀少。当前世界著名的博物馆虽有收藏，为数也寥寥无几。','钧瓷还经常作为国礼赠送国际友人和各国元首，世界各地收藏大家也将其作为珍品竞相收藏。 特别是祥瑞瓶、乾坤瓶、华夏瓶，在2003年、2004年，2005年博鳌亚洲论坛年会上，分别被指定为唯一国礼赠送给各国政要；2006、2007年钧瓷又作为唯一国礼在中国——东盟博览会上赠送给领导人；2008年钧瓷先后被作为中日友好纪念国礼赠送给日本政要、钧瓷作品“四海升平”被联合国总部永久收藏。钧瓷盛名更是蜚声海内外。 2003年，禹州市神垕镇等地被国家批准为钧瓷原产地，受到保护。国家级重点文物保护单位中，陶瓷窑遗址类禹州独占一半，其中禹州钧官窑遗址博物馆在作为国家级重点文物保护单位的同时也成为国家4A级旅游景区，同时也进入国家世界文化遗产预备名录。禹州钧瓷烧制工艺被列入国家非物质文化遗产名录。自2002年起，禹州市每年与中国工艺美术协会、中国陶瓷工艺协会等单位联合举办中国钧瓷文化节活动，以弘扬和提升禹州钧瓷这一中华瑰宝的深厚魅力。2008年禹州又因钧瓷文化而被列入河南省文化改革发展试验区。','中国历史文化名镇禹州市神垕镇的古街、古树、古民居，古玩、古瓷、古钧窑，为神垕古镇蒙上了神秘的色彩。古朴斑驳的十里老街，刻满岁月痕迹的幽深古民居，上千个高耸的烟囱，必将带你进入神垕的一世繁荣和千年的回忆。琳琅满目的古玩市场将带给你刺激和意想不到的惊喜。“神垕古镇”之名历史上曾被四次皇封。因至今仍盛产各种陶瓷，神垕古镇”被誉为全国唯一“活着的古镇”，先后成为国家小城镇建设重点镇、首批中国历史文化名镇、首批国家特色旅游文化名镇。']},{'type':'title','value':'中华药都'},{'type':'content','value':['全国中医药材之都','禹州市中药材种植有悠久的历史，明朝洪武年间已成为全国四大药材集散地之一，其中药材种植具有得天独厚的条件，禹白芷、禹南星、禹白附、金银花等地产名药，名列李时珍《本草纲目》和现行中华药典，被列为国家保护品种。而中华药城的地位也使得禹州成为全国道地药材的销售中心。走进药材市场，可以随处看到零售、批发药材的各色小店铺，每户店面所陈列的药品都有百种以上。琳琅满目的药材充斥着人们的视觉神经，弥散在空气中的中药味道，使人置身于药材的世界，领略药材的清雅与厚重。禹州的中药材销售主要集中在药城路东侧的中药材专业交易市场（中华药城）内。','禹州地理气候优越，盛产动、植、矿物药材1084种，蕴藏量达到12万吨。唐朝药王孙思邈曾长期游居禹州，行医采药、著书立说，孙思邈去世后，禹州人民在西关购地建“药王阁”永久纪念。故有“药不经禹州不香，医不见药王不妙”之说。禹州的中药文化自此开始繁荣。宋仁宗年间，阳翟古城寺以东盛产白芷、南星、菊花、薏米等药材，时任河南主薄的著名诗人梅尧臣游览禹州，正值春和景明，翅花盛开之际，不禁诗兴大发，遂作诗一首：“云外阳翟山，实与嵩少接，山中采药人，能自辨苗叶，当须刨其根，以道素所惬”。','元世祖至元一年（1264年），禹州已成为药材汇集之区，填满街市，犹如粪土，故农家深山大壑采药者往来不绝。明洪武元年（1368年），朱元璋曾诏令全国药商来禹集结，每年春、秋、冬三个会期。“内而全国22省、外越西洋、南洋、东极高丽、北际库伦，皆每车节转而至，真可谓无街不药行，处处闻药香”。长期的药材交易，逐渐产生了以经营类别或区域性质划分的药行帮、药棚帮、甘草帮、党参帮、江西帮、山西帮、陕西帮、祁州帮、商城帮、亳州帮、金陵帮等18个帮会组织，继而形成禹州独特的中药文化。商帮建设了各自的会馆，其中规模最大的为怀帮会馆，也是全国保存最完整的药商会馆。成为中国禹州药会历史上的四大中药材集散地之一，被称为“中华药城”。','明代周定王朱橚到禹州考察采集标本，于1406年撰写的《救荒本草》共列可救荒植物414种。著有《普济方》168卷，收载医方61709个，为历代医方之最，在剧毒药材处理上，采取吸附分离技术等都具有极高的科学价值。清代禹州的“保光清凉散”、“九天阿胶”相继问世，行销全国，颇享盛誉。继之开业的名店药堂赵隆太，加工炮制的“九蒸九制大熟地”于宣统三年在德国柏林举办的“万国博览会”上参展。','禹州的中药材种植历史悠久，明嘉靖年间，已种植名贵地道药材45种，禹白芷、禹南星、禹白附已列入李时珍的《本草纲目》。解放后，中药材生产有较大发展，1969年全市出现200多个药材种植村。1985年3月中药材交流大会重新恢复，推动了中药材种植业发展。而禹州中药材炮制工艺、禹州药交会也因丰富的文化内涵成为国家非物质文化遗产。禹州中药加工炮制开始于明朝，清朝趋于规范。饮片加工及炮制因药制宜，技艺独特，制作精细，在国内久享盛誉，有“药进禹州倍生香”之说。在加工、炮制过程中，其“浸、泡、煅、煨、炒、炙、蒸、煮”等方面都有其独到之处。有的饮片加工后，细如丝、吹能飞，片片均匀，厚薄适宜。在炮制方法上以精益求精而久负盛名。','进入新时期以来，禹州中药材产业发展更加迅速。成为全国17家标准化、规范化的国家级中药材专业市场之一，也是河南唯一的国家级中药材专业市场。年交易额居全国第三位。在中药材贸易中的影响力愈加增强，被批准为“全国中药饮片配送中心”。同时标准化的中药材生产规模也进一步扩大，通过国家GAP认证的药材基地数量逐年增加。禹白芷、禹南星、禹白附、豫西丹参4种道地药材取得国家原产地域地理标志保护，在全国名列前茅。被农业部批准为全国唯一的中药材加工示范基地。“药不经禹州不香，医不见药王不妙”，两年一度的中国·禹州药王孙思邈国际医药文化节暨中医药交易大会不仅为禹州带来了交易收入，而且又把禹州与全国联系得更加紧密。']}]}];

